export default {
  name: 'assistantForm',
  props: {
    project: Object
  },
  watch: {
    'project.name': function (val) {
      this.name = val
    },
    'project.content': function (val) {
      this.content = val
    },
    'project.startTime': function (val) {
      this.startTime = val
    },
    'project.endTime': function (val) {
      this.endTime = val
    },
    'project.target': function (val) {
      this.target = val
    }
  },
  data () {
    return {
      name: '',
      startTime: '',
      endTime: '',
      content: '',
      target: '',
      list: []
    }
  },
  methods: {
    submit () {
      this.$axios({
        method: 'post',
        url: this.url + '/project/',
        data: {
          id: this.project.id,
          name: this.name,
          creatorID: this.project.creatorID,
          leaderID: this.project.leaderID,
          startTime: this.startTime,
          endTime: this.endTime,
          content: this.content,
          target: this.target,
          tagSet: [{
            id: parseInt(localStorage.getItem('id')),
            tag: true
          }]
        }
      }).then((res) => {
        if (res.status === 200) alert('提交成功！')
        else alert('1')
      }).catch((err) => {
        alert(err)
      })
      // this.$axios({
      //   method: 'post',
      //   url: this.url + '/project/',
      //   data: JSON.stringify(this.item)
      // }).then((res) => {
      //   if (res.status === 200) alert('成功创建项目2！')
      //   else alert('2')
      // }).catch((err) => {
      //   alert(err)
      // })
    }
  }
}



