import ExamineHeader from './examineComponents/header'
import ExamineDetail from './examineComponents/detail'
import axios from 'axios/index'

export default {
  name: 'examine',
  components: {
    ExamineHeader,
    ExamineDetail
  },
  data () {
    return {
      project: Object,
      name: String
    }
  },
  methods: {
    getProject () {
      axios.get(this.url + '/project/id/' + this.$route.params.pId)
        .then(this.getProjectSucc)
    },
    getProjectSucc (res) {
      if (res.status !== 200) alert(res)
      else {
        res = res.data
        this.project = res
        console.log(this.project)
        this.name = res.name
      }
    }
  },
  mounted () {
    this.getProject()
  }
}



